<template>
    <div class="main" >
        <div class="under_header px-2 pt-1 d-flex justify-content-between ">
            <div class="news_title">
                <h5>For you</h5>
            </div>
              <div v-if="!showWeaterTemp" class="fa-1x spiner pe-2">
                   <i class="fas fa-circle-notch fa-spin"></i>
                </div>
            <div v-else class="wearher d-flex">
                <div class="wearher_img px-1">
                    <img :src="showWeaterImg" alt="">
                </div>
                <div class="wearher_temp">
                    <strong>
                        {{showWeaterTemp}}°C
                    </strong>
                </div>
            </div>
        </div>
     
        <div class="lines pt-3 pb-3"><hr></div>

        <div 
           v-for="(news, i) in allNews" :key="i"
            class="news px-2 py-2">
            <router-link 
                @click="setNews(news)"
                class="routerLink"
                :to="{name: 'newsPage', params:{ name: `${news.source.name}`} }"
            >
                <div class="d-flex ">
                        <div class="flex-shrink-0 img_news">
                            <img
                            v-show="news.urlToImage" 
                            :src="news.urlToImage" alt="">
                        </div>
                        <div class="content_news px-2 d-flex flex-column">
                            <div class="contect_category ">
                                <a :href="news.url">
                                    {{news.source.name}}
                                </a>
                            </div>
                            <div class="contect_title ">
                                <strong>{{news.title}}</strong>
                            </div>
                            <div class="content_time_ago">
                                <p>{{news.dateAgo}}</p>
                            </div>
                
                        </div>
                </div>
               
            </router-link>
        </div>   
        
        
    </div>
    
</template>

<script>

import {mapGetters,mapActions, mapMutations} from 'vuex'
export default {
    name:'NewsBlock',

    computed:{
        ...mapGetters(['allNews','allWeather','showWeaterTemp','showWeaterImg']),
    },
    methods: {
        ...mapActions(['fetchNews','weatherFetch']),
        ...mapMutations(['updateTargetNews']),
        setNews(news){
            this.updateTargetNews(news)
        }
       
       
    },
    watch:{
        clickNavBar(newV){
            if (newV) {
                return this.statusBar = 'block'
            }
            return ''
        }
    },
   
    mounted(){
        this.fetchNews();
         navigator.geolocation.getCurrentPosition((position=>{
            let latitude = position.coords.latitude;
            let longitude = position.coords.longitude;
                localStorage.setItem('latitude',latitude);
                localStorage.setItem('longitude',longitude);
            return this.weatherFetch(`${localStorage.getItem('latitude')},${localStorage.getItem('longitude')}`)
        }));
        if(localStorage.getItem('latitude') && localStorage.getItem('longitude')){
           this.weatherFetch(`${localStorage.getItem('latitude')},${localStorage.getItem('longitude')}`)
        }else{
            this.weatherFetch();
        }
        
        
        
        
        
        
    }
    

    
}
</script>

<style scoped>
       
*{
    margin: 0;
    
}
.news a{
    text-decoration: none;
    color: black;
}

.img_news img{
    width: 100px;
    height: 100px;
    object-fit:cover;
    border-radius: 10%;
}
.contect_title strong{
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    line-height: 20px;
    max-height: 60px;
    
}
.content_time_ago{
    color: #B7B7B7;
}
.contect_category a{
    color: #eb3b3b;
    text-decoration: none;
}
.contect_category a span{ 
    font-size: 12px;
    color: #B7B7B7;
    text-decoration: none;
}
.wearher_img img {
    width: 24px;
    height: 25px;
    object-fit:cover;
}
.main{
    padding-top: 65px;
}


.active{
    color: #eb3b3b;
    transform: scale(1.12);
    transition: 0.2s ease-in-out;
    
    
}


</style>